				"html": "<p>In Ruby you can perform all standard math operations on numbers, including: addition <code>+</code>, subtraction <code>-</code>, multiplication <code>*</code>, division <code>/</code>, find remainders <code>%</code>, and work with exponents <code>**</code>.</p>\n<h2>Addition:</h2>\n<ul>\n<li>\n<p>Numbers can be added together using the <code>+</code> operator.</p>\n<p>15 + 25</p>\n<h1>returns:</h1>\n<p>40</p>\n</li>\n</ul>\n<h2>Subtraction:</h2>\n<ul>\n<li>\n<p>Numbers can be subtracted from one another using the <code>-</code> operator.</p>\n<p>25 - 15</p>\n<h1>returns:</h1>\n<p>10</p>\n</li>\n</ul>\n<h2>Multiplication:</h2>\n<ul>\n<li>\n<p>Numbers can be multiplied together using the <code>*</code> operator.</p>\n<p>10 * 5</p>\n<h1>returns:</h1>\n<p>50</p>\n</li>\n</ul>\n<h2>Division:</h2>\n<ul>\n<li>\n<p>Numbers can be divided by one another using the <code>/</code> operator.</p>\n<p>10 / 5</p>\n<h1>returns:</h1>\n<p>2</p>\n</li>\n</ul>\n<h2>Remainders:</h2>\n<ul>\n<li>\n<p>Remainders can be found using the modulus <code>%</code> operator.</p>\n<p>10 % 3</p>\n<h1>returns:</h1>\n<p>1 # because the remainder of 10/3 is 1</p>\n</li>\n</ul>\n<h2>Exponents:</h2>\n<ul>\n<li>\n<p>Exponents can be calculated using the <code>**</code> operator.</p>\n<p>2 ** 3</p>\n<h1>returns:</h1>\n<p>8 # because 2 to the third power, or 2 * 2 * 2 = 8</p>\n</li>\n</ul>",
